<template>
  <div class="h-full">
    <notifications
      position="top center"
      classes="notification-custom"
    />
    <router-view class="h-full" />
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import constant from '@/utils/constant';
export default {
  name: 'App',
  created() {
    this.checkLogin();
  },
  mounted() {
    // add js responsivevoice
    if (window.rvAgentPlayer === undefined) {
      let responseVoice = document.createElement('script');
      responseVoice.setAttribute('src', 'https://code.responsivevoice.org/responsivevoice.js?key='+constant.RESPONSIVEVOICE_KEY)
      document.head.appendChild(responseVoice);
    }
  },
  methods: {
    ...mapActions('authStore', [
      'checkLogin'
    ]),
  }
}
</script>
