import { createWebHistory, createRouter } from 'vue-router';
import LayoutFront from '@/views/layout/LayoutFront';
import FrontIndex from '@/views/front/FrontIndex';
import GameIndex from '@/views/front/game/GameIndex';
import GamePlay from '@/views/front/game/GamePlay';
import GameAdd from '@/views/front/game/GameAdd';
import AuthLogin from '@/views/auth/AuthLogin';
import store from '@/store';
import constant from '@/utils/constant';
import { nextTick } from 'vue';
import { encode, decode } from 'js-base64';

const routes = [
  {
    path: '/',
    name: 'home',
    redirect: '/',
    component: LayoutFront,
    children: [
      {
        path: '/',
        name: 'game',
        component: GameIndex,
        meta: {
          title: 'Game',
        },
      },
      {
        path: '/game/play/:id',
        name: 'game_play',
        component: GamePlay,
        meta: {
          title: 'Game',
        },
      },
      {
        path: '/game/add',
        name: 'game_add',
        component: GameAdd,
        meta: {
          title: 'Game Add',
        },
      },
      {
        path: '/game/edit/:id',
        name: 'game_edit',
        component: GameAdd,
        meta: {
          title: 'Game edit',
        },
      },
    ],
  },
  // {
  //   path: '/login',
  //   name: 'login',
  //   component: AuthLogin,
  //   meta: {
  //     title: 'Login',
  //   },
  // },
  { path: '/:pathMatch(.*)*', redirect: '/' },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
// check route
router.beforeEach((to, _from, next) => {
  // set title
  store.dispatch('headerStore/setTitle', to.meta.title, { root: true })

  // // check route no auth
  // if (constant.ROUTE_NO_AUTH.includes(to.name)) {
  //   return next()
  // }

  // // user
  // const user = localStorage.getItem(constant.LOCAL_STORAGE_KEY.ACCESS_USER)
  // ? JSON.parse(decode(localStorage.getItem(constant.LOCAL_STORAGE_KEY.ACCESS_USER)))
  // : ''

  // // check login
  // if (!user?.username) {
  //   return next({
  //     name: 'login',
  //     params: { urlRedirect: to.fullPath, routeName: to.name },
  //   })
  // }

  return next();
})

router.afterEach((to) => {
  nextTick(() => {
    document.title = `${to.meta.title} | Game Letter` || 'Game Letter'
  })
})
