<template>
  <div
    class="flex justify-center min-h-[100vh] bg-[position:bottom,top] bg-repeat-x bg-[length:500px,cover]"
    :class="[
      randomBackground == 0
        ? 'bg-[url(../images/bg_bottom_1.svg),url(../images/bg_top_1.svg)]'
        : 'bg-[url(../images/bg_bottom_2.svg),url(../images/bg_top_2.svg)]',
    ]"
  >
    <div
      class="flex flex-col items-center py-[60px] px-[16px] md:w-[750px] w-full"
    >
      <h1 class="md:text-[30px] text-[20px] font-bold mb-5">
        Thêm mới trò chơi
      </h1>
      <div class="w-full">
        <!-- form -->
        <Form
          @submit="onSubmit"
          @invalid-submit="onInvalidSubmit"
        >
          <!-- game name -->
          <div class="mb-5">
            <label
              class="flex items-center text-left md:text-[16px] text-[14px] font-bold mb-2"
              for="game_name"
            >Tên trò chơi
              <p class="text-red-500">*</p></label>
            <Field
              v-model="gameData.name"
              type="text"
              name="game_name"
              placeholder="Nhập tên gợi nhớ cho trò chơi"
              rules="required|stringSpace|max:50"
              class="form-default w-[100%] md:p-[10px] p-[5px] text-[12px] md:text-[14px] rounded-xl"
            />
            <ErrorMessage
              class="text-red-500 text-[11px] flex"
              name="game_name"
            />
          </div>
          <!-- round list -->
          <div
            v-for="(round, roundIndex) in gameData.rounds"
            :key="roundIndex"
            class="bg-color-4 rounded-xl p-[8px] mb-4 relative"
          >
            <h4
              class="text-left text-color-5 md:text-[20px] text-[16px] font-bold mb-[16px]"
            >
              Vòng {{ roundIndex + 1 }}
            </h4>
            <!-- remove button -->
            <button
              v-if="shouldShowRemoveButton(gameData.rounds)"
              type="button"
              class="absolute top-1 right-1"
              @click="removeItemArray(gameData.rounds, roundIndex)"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="3"
                stroke="red"
                class="w-7 h-7"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </button>
            <!-- body round -->
            <div>
              <!-- title -->
              <div class="mb-[24px]">
                <div class="mb-2 text-left">
                  <label
                    class="flex font-bold md:text-[16px] text-[14px]"
                    :for="'title[' + roundIndex + ']'"
                  >Tiêu đề
                    <p class="text-red-500">*</p></label>
                  <p class="text-[9px] md:text-[12px]">
                    Tiêu đề ví dụ: Tìm chữ có vần tương ứng
                  </p>
                </div>
                <Field
                  v-model="round.title"
                  type="text"
                  placeholder="Nhập tiêu đề cho vòng chơi"
                  :name="'title[' + roundIndex + ']'"
                  rules="required|stringSpace|max:50"
                  class="form-default w-[100%] md:p-[10px] p-[5px] text-[12px] md:text-[14px] rounded-xl"
                />
                <ErrorMessage
                  class="text-red-500 text-[11px] flex"
                  :name="'title[' + roundIndex + ']'"
                />
              </div>
              <!-- timer -->
              <div class="mb-[24px]">
                <div class="mb-2 text-left">
                  <label
                    class="block font-bold md:text-[16px] text-[14px]"
                    :for="'timer[' + roundIndex + ']'"
                  >Thời gian (giây)</label>
                  <p class="text-[9px] md:text-[12px]">
                    Để trống nếu muốn không giới hạn thời gian
                  </p>
                </div>
                <Field
                  v-model.number="round.timer"
                  type="number"
                  placeholder="10"
                  :name="'timer[' + roundIndex + ']'"
                  rules="positiveNumber|min_value:1|max_value:600"
                  class="form-default w-[100%] md:p-[10px] p-[5px] text-[12px] md:text-[14px] rounded-xl"
                />
                <ErrorMessage
                  class="text-red-500 text-[11px] flex"
                  :name="'timer[' + roundIndex + ']'"
                />
              </div>
              <!-- challenge list -->
              <div>
                <div class="mb-2 text-left">
                  <label class="flex font-bold md:text-[16px] text-[14px]">Thử thách
                    <p class="text-red-500">*</p></label>
                </div>
                <!-- list -->
                <div
                  v-for="(challenge, challengeIndex) in round.challenges"
                  :key="challengeIndex"
                  class="p-[16px] border border-dashed border-color-5 rounded-lg mb-4 relative"
                >
                  <!-- remove button -->
                  <button
                    v-if="shouldShowRemoveButton(round.challenges)"
                    type="button"
                    class="absolute top-1 right-1"
                    @click="removeItemArray(round.challenges, challengeIndex)"
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke-width="3"
                      stroke="red"
                      class="w-5 h-5"
                    >
                      <path
                        stroke-linecap="round"
                        stroke-linejoin="round"
                        d="M6 18L18 6M6 6l12 12"
                      />
                    </svg>
                  </button>
                  <!-- question -->
                  <div>
                    <div class="mb-1">
                      <label
                        class="flex items-center text-left text-[12px] font-bold"
                        :for="
                          'challenge_question[' +
                            roundIndex +
                            '][' +
                            challengeIndex +
                            ']'
                        "
                      >Câu hỏi</label>
                      <p class="text-[9px] md:text-[12px] text-left">
                        Để trống nếu muốn tăng độ khó
                      </p>
                    </div>
                    <Field
                      v-model="challenge.question"
                      type="text"
                      placeholder="a"
                      :name="
                        'challenge_question[' +
                          roundIndex +
                          '][' +
                          challengeIndex +
                          ']'
                      "
                      :rules="`stringSpace|max:100|arrayDuplicate:${convertQuestionToString(
                        round.challenges
                      )}`"
                      class="form-default w-[100%] md:p-[10px] p-[5px] text-[12px] md:text-[14px] rounded-xl"
                    />
                    <ErrorMessage
                      class="text-red-500 text-[11px] flex"
                      :name="
                        'challenge_question[' +
                          roundIndex +
                          '][' +
                          challengeIndex +
                          ']'
                      "
                    />
                  </div>
                  <!-- answer -->
                  <div
                    v-for="(answer, answerIndex) in challenge.answers"
                    :key="answerIndex"
                    class="flex mt-3"
                  >
                    <div class="flex w-[85%]">
                      <div class="w-[70%] mr-[5%]">
                        <label
                          class="flex items-center text-left text-[12px] mb-1 font-bold"
                          :for="
                            'answer_value[' +
                              roundIndex +
                              '][' +
                              challengeIndex +
                              '][' +
                              answerIndex +
                              ']'
                          "
                        >Câu trả lời
                          <p class="text-red-500">*</p></label>
                        <Field
                          v-model="answer.value"
                          type="text"
                          placeholder="a"
                          :name="
                            'answer_value[' +
                              roundIndex +
                              '][' +
                              challengeIndex +
                              '][' +
                              answerIndex +
                              ']'
                          "
                          :rules="`required|stringSpace|max:100|arrayDuplicate:${convertAnswerToString(
                            challenge.answers
                          )}`"
                          class="form-default w-[100%] md:p-[10px] p-[5px] text-[12px] md:text-[14px] rounded-xl"
                        />
                        <ErrorMessage
                          class="text-red-500 text-[11px] flex"
                          :name="
                            'answer_value[' +
                              roundIndex +
                              '][' +
                              challengeIndex +
                              '][' +
                              answerIndex +
                              ']'
                          "
                        />
                      </div>
                      <div class="w-[25%]">
                        <label
                          class="flex items-center text-left text-[12px] mb-1 font-bold"
                          :for="
                            'answer_quantity[' +
                              roundIndex +
                              '][' +
                              challengeIndex +
                              '][' +
                              answerIndex +
                              ']'
                          "
                        >Số lượng
                          <p class="text-red-500">*</p></label>
                        <Field
                          v-model.number="answer.quantity"
                          type="number"
                          placeholder="1"
                          :name="
                            'answer_quantity[' +
                              roundIndex +
                              '][' +
                              challengeIndex +
                              '][' +
                              answerIndex +
                              ']'
                          "
                          :rules="`required|positiveNumber|min_value:1|max_value:30|maxQuantityRound:${totalQuantityRound(
                            round.challenges
                          )}`"
                          class="form-default w-[100%] md:p-[10px] p-[5px] text-[12px] md:text-[14px] rounded-xl"
                        />
                        <ErrorMessage
                          class="text-red-500 text-[11px] text-left flex"
                          :name="
                            'answer_quantity[' +
                              roundIndex +
                              '][' +
                              challengeIndex +
                              '][' +
                              answerIndex +
                              ']'
                          "
                        />
                      </div>
                    </div>
                    <div class="w-[10%] flex justify-center pt-[20px]">
                      <button
                        v-if="shouldShowRemoveButton(challenge.answers)"
                        class="cursor-pointer"
                        type="button"
                        @click="removeItemArray(challenge.answers, answerIndex)"
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke-width="1.5"
                          stroke="currentColor"
                          class="w-6 h-6"
                        >
                          <path
                            stroke-linecap="round"
                            stroke-linejoin="round"
                            d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0"
                          />
                        </svg>
                      </button>
                    </div>
                  </div>
                  <!-- add answer -->
                  <button
                    type="button"
                    class="btn-default w-[85%] mt-[16px]"
                    @click="addAnswer(challenge.answers)"
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke-width="3"
                      stroke="currentColor"
                      class="md:w-6 md:h-6 w-4 h-4"
                    >
                      <path
                        stroke-linecap="round"
                        stroke-linejoin="round"
                        d="M12 4.5v15m7.5-7.5h-15"
                      />
                    </svg>
                    <span class="md:ml-4 ml-2 md:text-[16px] text-[12px]">Thêm câu trả lời</span>
                  </button>
                </div>
                <!-- add challenge -->
                <div
                  class="p-[16px] border border-dashed border-color-5 rounded-lg"
                >
                  <!-- add challenge -->
                  <button
                    type="button"
                    class="btn-default w-full"
                    @click="addChallenge(round.challenges)"
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke-width="3"
                      stroke="currentColor"
                      class="md:w-6 md:h-6 w-4 h-4"
                    >
                      <path
                        stroke-linecap="round"
                        stroke-linejoin="round"
                        d="M12 4.5v15m7.5-7.5h-15"
                      />
                    </svg>
                    <span class="md:ml-4 ml-2 md:text-[16px] text-[12px]">Thêm thử thách</span>
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div class="bg-color-4 rounded-xl p-[8px]">
            <!-- add round -->
            <button
              type="button"
              class="btn-default w-[100%]"
              @click="addRound(gameData.rounds)"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="3"
                stroke="currentColor"
                class="md:w-6 md:h-6 w-4 h-4"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M12 4.5v15m7.5-7.5h-15"
                />
              </svg>

              <span class="md:ml-4 ml-2 md:text-[16px] text-[12px]">Thêm vòng chơi</span>
            </button>
          </div>
          <!-- control button -->
          <div class="flex justify-center mt-6">
            <button
              class="btn-default btn-default--blue mr-4 flex-shrink-0 md:text-[16px] text-[12px]"
              type="button"
              @click="
                $router.push({
                  name: 'game',
                })
              "
            >
              Trở về
            </button>
            <button
              class="btn-default btn-default--purple w-[100px] flex-shrink-0 md:text-[16px] text-[12px]"
              type="submit"
            >
              Lưu lại
            </button>
          </div>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
import { Form, Field, ErrorMessage } from 'vee-validate';
import { localize } from '@vee-validate/i18n';
import constant from '@/utils/constant';
export default {
  components: {
    Form,
    Field,
    ErrorMessage,
  },
  data() {
    return {
      gameData: {
        name: null,
        rounds: [
          {
            title: null,
            timer: null,
            challenges: [
              {
                question: null,
                answers: [
                  {
                    value: null,
                    quantity: null,
                  },
                ],
              },
            ],
          },
        ],
      },
      gameId: null,
    };
  },
  computed: {
    randomBackground() {
      return Math.floor(Math.random() * (1 - 0 + 1)) + 0;
    },
  },
  mounted() {
    this.checkEdit();
    // custom error message
    localize('vi', {
      fields: {
        game_name: {
          required: 'Vui lòng nhập tên trò chơi',
        },
      },
    });
  },
  methods: {
    /**
     * Check edit
     */
    checkEdit() {
      const routeName = this.$route.name;
      if (routeName == 'game_edit') {
        this.gameId = this.$route?.params?.id;
        if (this.gameId) {
          try {
            const gameData =
              localStorage.getItem(
                constant.LOCAL_STORAGE_KEY.GAME_ID_PREFIX + this.gameId
              ) || null;
            // Check data existence
            if (!gameData) {
              this.$notify({
                text: 'Dữ liệu game không tồn tại!', // Notify if game data does not exist
                type: 'error',
              });
              return this.$router.push({ name: 'game' });
            }
            this.gameData = JSON.parse(gameData);
            return true;
          } catch (e) {
            this.$notify({
              text: 'Dữ liệu game không tồn tại!', // Notify if an error occurs while getting game data
              type: 'error',
            });
            return this.$router.push({ name: 'game' });
          }
        } else {
          this.$notify({
            text: 'Dữ liệu game không tồn tại!', // Notify if an error occurs while getting game data
            type: 'error',
          });
          return this.$router.push({ name: 'game' });
        }
      }
    },
    /**
     * Submit form
     */
    onSubmit() {
      this.createGame();
    },
    /**
     * Check valid submit form
     */
    onInvalidSubmit({ values, errors, results }) {
      if (errors) {
        this.$notify({
          text: 'Có dữ liệu không hợp lệ, vui lòng kiểm tra lại biểu mẫu',
          type: 'error',
        });
      }
    },
    /**
     * Create game data to local storage
     */
    createGame() {
      const isValid = this.isDataValid(this.gameData);
      if (isValid.length > 0) {
        isValid.forEach((message) => {
          this.$notify({
            text: message,
            type: 'error',
          });
        })
        return false;
      }

      // update or create
      const gameId =
        constant.LOCAL_STORAGE_KEY.GAME_ID_PREFIX +
        (this.gameId ? this.gameId : this.generateId());
      localStorage.setItem(gameId, JSON.stringify(this.gameData));
      this.$router.push({ name: 'game' });
    },
    /**
     * validate data question
     */
    isDataValid(data) {
      const errors = [];
      let isQuantityValid = true;

      for (const round of data.rounds) {
        let hasQuestion = false; // Reset hasQuestion for each round
        let roundQuantity = 0; // Initialize the total quantity for the round

        for (const challenge of round.challenges) {
          if (challenge.question !== null && challenge.question.trim() !== '') {
            hasQuestion = true;
          }

          const challengeQuantity = challenge.answers.reduce((sum, answer) => sum + answer.quantity, 0);
          roundQuantity += challengeQuantity; // Accumulate the challenge quantity for the round

          if (challengeQuantity > 30) {
            isQuantityValid = false;
          }
        }

        if (roundQuantity > 30) {
          isQuantityValid = false;
        }

        if (!hasQuestion) {
          errors.push(`Vòng "${round.title}" vui lòng ít nhất có một câu hỏi.`);
        }

        if (!isQuantityValid) {
          errors.push(`Vòng "${round.title}" tổng số lượng câu trả lời không được quá 30.`);
        }
      }

      return errors;
    },
    /**
     * Add a answer
     */
    addAnswer(answers) {
      answers.push({ value: null, quantity: null });
    },
    /**
     * Add challenge
     */
    addChallenge(challenges) {
      challenges.push({
        question: null,
        answers: [
          {
            value: null,
            quantity: null,
          },
        ],
      });
    },
    /**
     * add a round
     */
    addRound(rounds) {
      rounds.push({
        title: null,
        timer: null,
        challenges: [
          {
            question: null,
            answers: [
              {
                value: null,
                quantity: null,
              },
            ],
          },
        ],
      });
    },
    /**
     * Remove item array
     */
    removeItemArray(array, index) {
      if (array.length > 1) {
        array.splice(index, 1);
      }
    },
    /**
     * check show remove button
     */
    shouldShowRemoveButton(array) {
      return array.length > 1;
    },
    /**
     * convert array to string
     */
     convertQuestionToString(array) {
      return array.map(element => element.question).join('___');;
    },
    /**
     * convert array to string
     */
     convertAnswerToString(array) {
      return array.map(element => element.value).join('___');;
    },
    /**
     * Total quantity a round
     */
    totalQuantityRound(challenges) {
      let total = 0;
      challenges.forEach((challenge) => {
        challenge.answers.forEach((answer) => {
          total += answer.quantity;
        })
      })
      return total;
    },
    /**
     * random id game
     */
    generateId() {
      const characters = 'abcdefghijklmnopqrstuvwxyz0123456789';
      let id = '';

      for (let i = 0; i < 8; i++) {
        const randomIndex = Math.floor(Math.random() * characters.length);
        id += characters.charAt(randomIndex);
      }
      return id;
    },
  },
};
</script>
