<template>
  <div
    class="flex justify-center min-h-[100vh] bg-[position:bottom,top] bg-repeat-x bg-[length:500px,cover]"
    :class="[ randomBackground == 0 ? 'bg-[url(../images/bg_bottom_1.svg),url(../images/bg_top_1.svg)]' : 'bg-[url(../images/bg_bottom_2.svg),url(../images/bg_top_2.svg)]']"
  >
    <div
      class="flex flex-col items-center py-[60px] px-[16px] md:w-[750px] w-full"
    >
      <h1 class="md:text-[30px] text-[20px] font-bold mb-5">
        Danh sách trò chơi
      </h1>
      <div class="mb-5">
        <button
          class="btn-default flex-shrink-0 mr-2"
          type="button"
          @click="$router.push({
            name: 'game_add'
          })"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke-width="3"
            stroke="currentColor"
            class="md:w-6 md:h-6 w-4 h-4"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              d="M12 4.5v15m7.5-7.5h-15"
            />
          </svg>
          <span
            class="md:ml-4 ml-2 md:text-[16px] text-[12px]"
          >Thêm mới trò chơi</span>
        </button>
      </div>
      <div class="h-[500px] overflow-auto w-full">
        <div
          v-for="(game, gameIndex) in savedGames"
          :key="gameIndex"
          class="flex items-center justify-between bg-color-4 w-full rounded-xl text-left p-[16px] mb-4"
        >
          <div class="text-color-5 font-bold pr-[16px] md:text-[14px] text-[12px]">
            {{ game.gameData.name }}
          </div>
          <div class="flex">
            <button
              class="btn-default btn-default--red flex-shrink-0 mr-2"
              type="button"
              @click="confirmDeleteGame(game.gameId, game.gameData.name)"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="2.5"
                stroke="#ffffff"
                class="md:w-6 md:h-6 w-4 h-4"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0"
                />
              </svg>
            </button>
            <button
              class="btn-default btn-default--blue flex-shrink-0 mr-2"
              type="button"
              @click="$router.push({
                name: 'game_edit',
                params: {
                  id: game.gameId
                }
              })"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
