                stroke="#ffffff"
                class="md:w-6 md:h-6 w-4 h-4"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M16.862 4.487l1.687-1.688a1.875 1.875 0 112.652 2.652L6.832 19.82a4.5 4.5 0 01-1.897 1.13l-2.685.8.8-2.685a4.5 4.5 0 011.13-1.897L16.863 4.487zm0 0L19.5 7.125"
                />
              </svg>
            </button>
            <button
              class="btn-default btn-default--purple flex-shrink-0"
              type="button"
              @click="$router.push({
                name: 'game_play',
                params: {
                  id: game.gameId
                }
              })"
            >
              <svg
                data-v-7580edd3=""
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="3"
                stroke="#ffffff"
                class="md:w-6 md:h-6 w-4 h-4"
              ><path
                data-v-7580edd3=""
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M5.25 5.653c0-.856.917-1.398 1.667-.986l11.54 6.348a1.125 1.125 0 010 1.971l-11.54 6.347a1.125 1.125 0 01-1.667-.985V5.653z"
              /></svg>
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
    <div
      id="modalConfirm"
      data-te-modal-init
      class="fixed left-0 top-0 z-[1055] hidden h-full w-full overflow-y-auto overflow-x-hidden outline-none"
      tabindex="-1"
      aria-labelledby="modalConfirmLabel"
      aria-hidden="true"
      data-te-backdrop="static"
    >
      <div
        data-te-modal-dialog-ref
        class="pointer-events-none relative flex min-h-[calc(100%-1rem)] w-auto translate-y-[-50px] items-center opacity-0 transition-all duration-300 ease-in-out min-[576px]:mx-auto min-[576px]:mt-7 min-[576px]:min-h-[calc(100%-3.5rem)] min-[576px]:max-w-[500px] p-[16px]"
      >
        <div
          class="pointer-events-auto relative flex w-full flex-col md:rounded-[50px] rounded-[20px] border-none bg-white bg-clip-padding text-current shadow-lg outline-none"
        >
          <!--Modal body-->
          <div
            class="relative flex-auto p-4 pt-[16px]"
            data-te-modal-body-ref
          >
            <div>
              <div class="text-[12px] md:text-[14px]">
                Bạn có chắc chắn muốn xoá game?
              </div>
              <div class="text-color-5 font font-bold text-[12px] md:text-[14px]">
                {{ deleteGameName }}
              </div>
            </div>
            <!-- button control modal -->
            <div class="flex justify-center mt-5">
              <!-- close button -->
              <button
                class="btn-default btn-default--blue flex-shrink-0 mr-3 md:text-[16px] text-[12px]"
                data-te-modal-dismiss
              >
                Huỷ
              </button>
              <!-- start game -->
              <button
                class="btn-default btn-default--purple flex-shrink-0 flex items-center md:text-[16px] text-[12px]"
                @click="deleteGame"
              >
                Đồng ý
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Modal, Ripple, initTE } from 'tw-elements';
import constant from '@/utils/constant';
export default {
  data() {
    return {
      savedGames: [],
      deleteGameId: null,
      deleteGameName: null,
    };
  },
  computed: {
    randomBackground() {
      return Math.floor(Math.random() * (1 - 0 + 1)) + 0;
    },
  },
  mounted() {
    // load saved games
    this.loadSavedGames();
    // create modal confirm
    this.modalEl = document.getElementById('modalConfirm');
    this.modalConfirm = new Modal(this.modalEl);
    initTE({ Modal, Ripple });
  },
  methods: {
    /**
     * Load saved games
     */
    loadSavedGames() {
      const savedGames = [];
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i);
        if (key.startsWith(constant.LOCAL_STORAGE_KEY.GAME_ID_PREFIX)) {
          const gameId = key.replace(
            constant.LOCAL_STORAGE_KEY.GAME_ID_PREFIX,
            ''
          );
          const gameData = JSON.parse(localStorage.getItem(key));
          savedGames.push({ gameId, gameData });
        }
      }
      this.savedGames = savedGames;
    },
    /**
     * Delete game confirm
     */
    confirmDeleteGame(gameId, gameName) {
      this.deleteGameId = gameId;
      this.deleteGameName = gameName;
      this.modalConfirm.show();
    },
    /**
     * Delete game confirm
     */
     deleteGame() {
      this.modalConfirm.hide();
      if (this.deleteGameId) {
        localStorage.removeItem(constant.LOCAL_STORAGE_KEY.GAME_ID_PREFIX + this.deleteGameId);
        this.loadSavedGames(); // get new data
        this.$notify({
            text: 'Xoá thành công',
            type: 'success',
        });
        return;
      }
      this.$notify({
          text: 'Xoá không thành công',
          type: 'error',
      });
    }
  },
};
</script>
<style lang="scss" scoped></style>
