<template>
  <div class="relative h-full">
    <img
      class="absolute bottom-0 left-0 md:w-[300px] w-[150px]"
      :src="images.bottom_left[randomBackground]"
    >
    <img
      class="absolute bottom-0 right-0 md:w-[300px] w-[150px]"
      :src="images.bottom_right[randomBackground]"
    >
    <div class="flex justify-between py-[10px] px-[16px]">
      <div class="text-left flex items-center font-bold">
        <!-- close button -->
        <button
          class="btn-default btn-default--blue flex-shrink-0 mr-3"
          @click="exitGame"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke-width="3"
            stroke="#ffffff"
            class="md:w-6 md:h-6 w-4 h-4"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              d="M15.75 9V5.25A2.25 2.25 0 0013.5 3h-6a2.25 2.25 0 00-2.25 2.25v13.5A2.25 2.25 0 007.5 21h6a2.25 2.25 0 002.25-2.25V15M12 9l-3 3m0 0l3 3m-3-3h12.75"
            />
          </svg>
        </button>
      </div>
      <div class="font-bold flex flex-col items-center justify-center">
        <div class="flex md:text-[25px] text-[14px] text-color-5 uppercase">
          {{ roundTitle }}
        </div>
        <div class="flex justify-center">
          <div
            v-for="(item, index) in findElement"
            :key="index"
          >
            <div v-if="questionIndex == index">
              <span class="text-rose-500 md:text-[22px] text-[14px] mr-2">{{
                item.question
              }}</span>
              <span
                class="md:text-[22px] text-[14px]"
                :class="{ 'text-emerald-500': item.found == item.quantity }"
              >({{ item.found }}/{{ item.quantity }})</span>
            </div>
          </div>
        </div>
      </div>
      <div class="text-right flex flex-col justify-center font-bold">
        <div class="flex md:mb-3 mb-1">
          <img
            :src="images.icon_flag"
            class="md:w-[20px] w-[10px] mr-3"
          >
          <span class="text-color-5 md:text-[16px] text-[12px]">{{ round }} / {{ totalRound }}</span>
        </div>
        <div class="flex">
          <img
            :src="images.icon_hourglass"
            class="md:w-[20px] w-[10px] mr-3"
          >
          <span
            class="text-color-5 min-w-[50px] text-left md:text-[16px] text-[12px]"
          >{{ timer || timerExpired ? `${timer}s` : '∞' }}</span>
        </div>
      </div>
    </div>
    <div
      class="wrap-game md:h-[calc(80vh-91px)] h-[calc(80vh-65px)] md:pt-[40px] pt-[20px] md:pb-[100px] pb-[40px] md:px-[100px] px-[40px]"
    >
      <div class="box-game h-[100%] relative">
        <div
          v-for="(item, index) in randomElement"
          :key="index"
          :style="{
            top: item.top,
            left: item.left,
            border: isItemSelected(index) ? '2px solid red' : '',
          }"
          class="absolute px-[10px] py-[10px] md:text-[25px] text-[16px] cursor-pointer rounded-md select-none"
          @click="checkResult(item.value, index)"
        >
          {{ item.value }}
        </div>
      </div>
    </div>
  </div>
  <!-- Modal -->
  <div
    id="modalStartGame"
    data-te-modal-init
    class="fixed left-0 top-0 z-[1055] hidden h-full w-full overflow-y-auto overflow-x-hidden outline-none"
    tabindex="-1"
    aria-labelledby="modalStartGameLabel"
    aria-hidden="true"
    data-te-backdrop="static"
  >
    <div
      data-te-modal-dialog-ref
      class="pointer-events-none relative flex min-h-[calc(100%-1rem)] w-auto translate-y-[-50px] items-center opacity-0 transition-all duration-300 ease-in-out min-[576px]:mx-auto min-[576px]:mt-7 min-[576px]:min-h-[calc(100%-3.5rem)] min-[576px]:max-w-[500px] p-[16px]"
    >
      <div
        class="pointer-events-auto relative flex w-full flex-col rounded-[50px] border-none bg-white bg-clip-padding text-current shadow-lg outline-none"
      >
        <!--Modal body-->
        <div
          class="relative flex-auto p-4 pt-[50px]"
          data-te-modal-body-ref
        >
          <div>
            <CommonConfetti
              v-if="isEndGame"
              class="absolute top-0 right-0 left-0 bottom-0"
            />
            <div
              class="md:text-[24px] text-[16px] font-bold mb-5 py-[10px] px-[20px] md:rounded-[20px] rounded-[15px] inline-flex absolute md:top-[-25px] top-[-20px] left-[50%] translate-x-[-50%]"
              :class="{
                'bg-rose-500': isEndGame,
                'bg-orange-400': !timerExpired,
                'bg-gray-400': timerExpired,
              }"
            >
              <span
                v-if="isEndGame"
                class="text-white uppercase"
              >
                Chiến thắng
              </span>
              <span
                v-else-if="timerExpired"
                class="text-white uppercase"
              >
                Hết thời gian
              </span>
              <span
                v-else
                class="text-white uppercase"
              >Vòng {{ round }} / {{ totalRound }}</span>
            </div>
            <div
              v-if="isEndGame"
              class="absolute top-[-18px] left-0 md:w-[140px] w-[90px]"
            >
              <img :src="images.winner_left">
            </div>
            <div
              v-if="isEndGame"
              class="absolute top-[-18px] right-0 md:w-[140px] w-[90px]"
            >
              <img :src="images.winner_right">
            </div>
            <div class="mb-5 flex flex-col items-center">
              <img
                v-if="isEndGame"
                class="md:w-[160px] w-[120px]"
                :src="images.medal[4]"
              >
              <img
                v-else-if="timerExpired"
                class="md:w-[80px] w-[60px]"
                :src="images.animals.lose.cat"
              >
              <img
                v-else-if="round > 1"
                :class="{
                  'md:w-[50px] w-[30px]': round <= 4,
                  'md:w-[80px] w-[60px]': round > 4,
                }"
                :src="round < 5 ? images.medal[round - 2] : images.medal[3]"
              >
              <img
                v-else
                class="md:w-[80px] w-[60px]"
                :src="images.animals.start[randomAnimal]"
              >
              <div class="font-bold text-[12px] md:text-[14px]">
                <span
                  v-if="isEndGame"
                  class="md:text-[18px] text-[14px] text-rose-500"
                >
                  Xin chúc mừng! bạn đã thắng trò chơi.
                </span>
                <span v-else-if="timerExpired">
                  Rất tiếc! bạn thua mất rồi
                </span>
                <span
                  v-else-if="round > 1"
                  class="text-pink-500"
                >Xin chúc mừng! bạn đã vượt qua vòng {{ round - 1 }}<br>Với
                  thời gian là {{ timerCount }} giây</span>
                <span
                  v-else
                  class="text-pink-500"
                >Xin chào! hãy cố gắng chiến thắng nhé<br>Nhấn bắt đầu để
                  chơi game</span>
              </div>
            </div>
            <div
              v-if="isEndGame"
              class="flex flex-col items-start text-left bg-color-4 text-[18px] p-[16px] rounded-xl max-h-[200px] overflow-auto"
            >
              <div
                class="text-color-5 font-bold flex md:text-[16px] text-[14px]"
              >
                Thống kê:
              </div>
              <div
                v-for="(item, index) in timerCountList"
                :key="item"
                class="md:text-[16px] text-[14px]"
              >
                <strong>- Vòng {{ index + 1 }}</strong>
                <div class="pl-[16px]">
                  <div class="flex items-center">
                    + Huy hiệu đạt được:
                    <img
                      :class="index < 3 ? 'w-[15px]' : 'w-[20px]'"
                      :src="index < 4 ? images.medal[index] : images.medal[3]"
                    >
                  </div>
                  + Thời gian chơi: {{ item }} giây
                </div>
              </div>
              <div class="md:text-[16px] text-[14px]">
                <strong>- Tổng thời gian:</strong> {{ totalTimerCount }} giây
              </div>
            </div>
            <div
              v-else-if="!timerExpired"
              class="flex flex-col items-start text-left bg-color-4 text-[18px] p-[16px] rounded-xl"
            >
              <div>
                <div
                  class="text-color-5 font-bold flex md:text-[16px] text-[14px]"
                >
                  Nhiệm vụ
                  <span
                    v-if="round > 1"
                    class="text-color-5 ml-1"
                  >tiếp theo</span>:
                </div>
                <div class="md:text-[16px] text-[14px]">
                  - {{ roundTitle }}
                </div>
                <div
                  v-for="(item, index) in findElement"
                  :key="index"
                  class="mr-5 md:text-[16px] text-[14px] pl-3"
                >
                  + <strong>{{ item.question }}</strong> (số lượng: {{ item.quantity }})
                </div>
              </div>
              <div
                v-if="timer"
                class="md:text-[16px] text-[14px]"
              >
                - Trong thời gian <strong>{{ timer }}</strong> giây
              </div>
            </div>
          </div>
          <!-- button control modal -->
          <div class="flex justify-center mt-5">
            <!-- close button -->
            <button
              class="btn-default btn-default--blue flex-shrink-0 mr-3 md:text-[16px] text-[12px]"
              @click="exitGame"
            >
              Thoát
            </button>
            <!-- reset game -->
            <button
              v-if="timerExpired || isEndGame"
              class="btn-default btn-default--purple flex-shrink-0 flex items-center md:text-[16px] text-[12px]"
              @click="resetGame"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="3"
                stroke="#ffffff"
                class="md:w-6 md:h-6 w-4 h-4 md:mr-4 mr-2"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3"
                />
              </svg>

              Chơi lại từ đầu
            </button>
            <!-- start game -->
            <button
              v-else
              class="btn-default btn-default--purple flex-shrink-0 flex items-center md:text-[16px] text-[12px]"
              @click="startGame"
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="3"
                stroke="#ffffff"
                class="md:w-6 md:h-6 w-4 h-4 md:mr-4 mr-2"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M5.25 5.653c0-.856.917-1.398 1.667-.986l11.54 6.348a1.125 1.125 0 010 1.971l-11.54 6.347a1.125 1.125 0 01-1.667-.985V5.653z"
                />
              </svg>

              Bắt đầu
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Modal, Ripple, initTE } from 'tw-elements';
import constant from '@/utils/constant';
import medal_round_1 from '@/assets/images/medal_round_1.svg';
import medal_round_2 from '@/assets/images/medal_round_2.svg';
import medal_round_3 from '@/assets/images/medal_round_3.svg';
import medal_round_4 from '@/assets/images/medal_round_4.svg';
import winner_left from '@/assets/images/winner_left.svg';
import winner_right from '@/assets/images/winner_right.svg';
import icon_hourglass from '@/assets/images/icon_hourglass.svg';
import icon_flag from '@/assets/images/icon_flag.svg';
import icon_hand from '@/assets/images/icon_hand.svg';
import cup from '@/assets/images/cup.svg';
import dog from '@/assets/images/dog.svg';
import cat from '@/assets/images/cat.svg';
import buffalo from '@/assets/images/buffalo.svg';
import hedgehog from '@/assets/images/hedgehog.svg';
import rabbit from '@/assets/images/rabbit.svg';
import bottom_right_1 from '@/assets/images/bottom_right_1.svg';
import bottom_left_1 from '@/assets/images/bottom_left_1.svg';
import bottom_right_2 from '@/assets/images/bottom_right_2.png';
import bottom_left_2 from '@/assets/images/bottom_left_2.png';
import bottom_right_3 from '@/assets/images/bottom_right_3.svg';
import bottom_left_3 from '@/assets/images/bottom_left_3.svg';
import CommonConfetti from '@/components/Common/CommonConfetti.vue';

export default {
  components: {
    CommonConfetti
  },
  data() {
    return {
      images: {
        winner_left,
        winner_right,
        icon_hourglass,
        icon_flag,
        icon_hand,
        bottom_left: [bottom_left_1, bottom_left_2, bottom_left_3],
        bottom_right: [bottom_right_1, bottom_right_2, bottom_right_3],
        medal: [
          medal_round_1,
          medal_round_2,
          medal_round_3,
          medal_round_4,
          cup,
        ],
        animals: {
          start: [dog, buffalo, hedgehog, rabbit],
          lose: {
            cat,
          },
        },
      },
      gameData: {},
      roundTitle: '',
      challengeElement: [],
      randomElement: [],
      resultIndexList: [],
      findElement: [],
      findTotal: [],
      round: 1,
      questionIndex: 0,
      totalRound: 1,
      timer: null,
      timerCount: 0,
      timerCountList: [],
      timerExpired: false,
      gameStart: false,
      intervalId: null,
      modalStartGame: null,
      modalEl: null,
      isResetGame: false,
      isEndGame: false,
    };
  },
  computed: {
    roundData() {
      return this.gameData.rounds[this.round - 1];
    },
    randomAnimal() {
      return Math.floor(Math.random() * (3 - 0 + 1)) + 0;
    },
    randomBackground() {
      return Math.floor(Math.random() * (2 - 0 + 1)) + 0;
    },
    totalTimerCount() {
      return this.timerCountList.reduce(
        (accumulator, currentValue) => accumulator + currentValue,
        0
      );
    },
  },
  async mounted() {
    // get data
    let result = await this.getData();
    if (result) {
      this.modalEl = document.getElementById('modalStartGame');
      this.modalStartGame = new Modal(this.modalEl);
      initTE({ Modal, Ripple });
      // create game
      this.createGame();
    }

    // call back when click back browser
    history.replaceState(null, null, document.URL);
    window.addEventListener('popstate', function (e) {
      let element = document.querySelector('[data-te-backdrop-show]');
      if (element) {
        element.remove();
      }
    });
  },
  methods: {
    /**
     * Get data game in localStorage
     */
    getData() {
      let gameId = this.$route?.params?.id;
      try {
        const gameData =
          localStorage.getItem(
            constant.LOCAL_STORAGE_KEY.GAME_ID_PREFIX + gameId
          ) || null;
        // Check data existence
        if (!gameData) {
          this.$notify({
            text: 'Dữ liệu game không tồn tại!', // Notify if game data does not exist
            type: 'error',
          });
          return this.$router.push({ name: 'game' });
        }
        this.gameData = JSON.parse(gameData);
        this.totalRound = this.gameData.rounds.length;
        return true;
      } catch (e) {
        this.$notify({
          text: 'Có lỗi xảy ra, vui lòng liên hệ người quản lý', // Notify if an error occurs while getting game data
          type: 'error',
        });
        return this.$router.push({ name: 'game' });
      }
    },

    /**
     * Create a new game round
     */
    createGame(round = 1) {
      this.questionIndex = 0;
      this.round = round;
      this.resultIndexList = [];
      this.timer = this.roundData.timer;
      this.roundTitle = this.roundData.title;
      this.challengeElement = this.roundData.challenges;

      let reduceResult = this.challengeElement.reduce(
        (result, challenge) => {
          if (challenge.question) {
            let totalQuantity = 0;
            challenge.answers.forEach((answer) => {
              answer.found = 0;
              totalQuantity += answer.quantity;
            });
            result.total += totalQuantity;
            challenge.quantity = totalQuantity;
            challenge.found = 0;
            result.output.push(challenge);
          }
          return result;
        },
        { output: [], total: 0 }
      );

      this.findElement = reduceResult.output;
      this.findTotal = reduceResult.total;

      this.prepareData();
      this.showModalStartGame();
    },

    /**
     * Reset the game
     */
    resetGame() {
      this.hideModalStartGame();
      clearInterval(this.intervalId);
      this.isResetGame = true;
      this.modalEl.addEventListener('hidden.te.modal', (e) => {
        if (this.isResetGame) {
          this.timerExpired = false;
          this.isResetGame = false;
          this.isEndGame = false;
          this.timerCountList = [];
          this.timerCount = 0;
          this.createGame();
        }
      });
    },

    /**
     * Start the timer
     */
    startTimer() {
      this.timerCount = 0;
      this.intervalId = setInterval(() => {
        this.timerCount++;
        // Check if round timer
        if (this.timer) {
          this.timer--;
          if (this.timer <= 0) {
            this.timerExpired = true;
            this.gameStart = false;
            clearInterval(this.intervalId);
            this.showModalStartGame();
          }
        }
      }, 1000); // Decrease the timer every second
    },

    /**
     * Start the game
     */
    startGame() {
      this.timerExpired = false;
      this.gameStart = true;
      this.startTimer(); // Start the timer

      this.hideModalStartGame();
    },

    /**
     * Exit the game
     */
    exitGame() {
      clearInterval(this.intervalId);
      this.hideModalStartGame();
      this.$router.push({
        name: 'game',
      });
    },

    /**
     * Check if an element is selected
     */
    isItemSelected(index) {
      return this.resultIndexList.includes(index);
    },

    /**
     * Show the start game modal
     */
    showModalStartGame() {
      this.modalStartGame.show();
    },

    /**
     * Hide the start game modal
     */
    hideModalStartGame() {
      this.modalStartGame.hide();
    },

    /**
     * Check the result when an element is clicked.
     */
    checkResult(value, index) {
      // Check if game start flag
      if (!this.gameStart) {
        this.$notify({
          text: 'Vui lòng nhấn nút bắt đầu!', // Notify if the game has not started
          type: 'error',
        });
        return;
      }

      // get current question
      if (this.findElement[this.questionIndex]) {
        let question = this.findElement[this.questionIndex];
        // check if all answers are correct
        if (question.found < question.quantity) {
          question.answers.forEach(item => {
            if (value == item.value && item.found < item.quantity && !this.resultIndexList.includes(index)) {
              // text to speech
              window.responsiveVoice.speak(value, 'Vietnamese Female');
              item.found += 1;
              question.found += 1;
              this.resultIndexList.push(index); // Add the selected element to the result
            }
          })

          //check if question current is done
          if (question.found  == question.quantity) {
            this.questionIndex += 1;
          }
        }
      }

      // Check if all correct elements are chosen, then go to the next round or end the game
      if (this.resultIndexList.length == this.findTotal) {
        this.gameStart = false;
        clearInterval(this.intervalId);
        this.timerCountList.push(this.timerCount);
        // Go to the next round
        if (this.round + 1 <= this.gameData.rounds.length) {
          this.createGame(this.round + 1);
        } else {
          // End of the game
          this.isEndGame = true;
          this.showModalStartGame();
        }
      }
    },
    /**
     * prepare data random in view
     */
    prepareData() {
      const containerWidth = 95; // Set the container width in percentage (95%)
      const containerHeight = 95; // Set the container height in percentage (95%)
      const minDistance = 20; // Minimum distance between elements in pixels
      const maxIterations = 1000; // Maximum iterations for the force layout algorithm

      // Create an array to store the transformed answers
      const transformedAnswers = [];

      // Iterate over the challengeElement array
      this.challengeElement.forEach((item) => {
        const { answers } = item;

        // Generate an array with repeated values based on the quantity
        answers.forEach((answer) => {
          const { value, quantity } = answer;

          // Add the value and quantity of the answer to the transformedAnswers array
          for (let i = 0; i < quantity; i++) {
            transformedAnswers.push({ value });
          }
        });
      });

      // Create initial positions for each answer
      const positions = transformedAnswers.map(() => ({
        x: Math.random() * containerWidth,
        y: Math.random() * containerHeight,
      }));

      // Apply force-directed layout algorithm
      for (let iteration = 0; iteration < maxIterations; iteration++) {
        let maxDisplacement = 0;

        for (let i = 0; i < transformedAnswers.length; i++) {
          const currentPosition = positions[i];
          let displacementX = 0;
          let displacementY = 0;

          for (let j = 0; j < transformedAnswers.length; j++) {
            if (i !== j) {
              const otherPosition = positions[j];
              const distanceX = otherPosition.x - currentPosition.x;
              const distanceY = otherPosition.y - currentPosition.y;
              const distance = Math.sqrt(
                distanceX * distanceX + distanceY * distanceY
              );
              const directionX = distanceX / distance;
              const directionY = distanceY / distance;

              if (distance < minDistance) {
                const minSeparation = minDistance - distance;
                displacementX -= minSeparation * directionX;
                displacementY -= minSeparation * directionY;
              }
            }
          }

          currentPosition.x += displacementX;
          currentPosition.y += displacementY;

          maxDisplacement = Math.max(
            maxDisplacement,
            Math.abs(displacementX),
            Math.abs(displacementY)
          );
        }

        if (maxDisplacement < 0.1) {
          break; // Stop the algorithm if the maximum displacement is below a threshold
        }
      }

      // Normalize positions and update transformedAnswers
      this.randomElement = transformedAnswers.map((item, index) => {
        let { x, y } = positions[index];

        // Handle overflow
        x = Math.max(0, Math.min(x, containerWidth));
        y = Math.max(0, Math.min(y, containerHeight));

        // Scale the positions to fit within 80% of container width and height
        x = (x * containerWidth) / 100;
        y = (y * containerHeight) / 100;

        return {
          value: item.value,
          top: y + '%',
          left: x + '%',
        };
      });
    },
  },
};
</script>
