import { defineRule, configure } from 'vee-validate';
import AllRules from '@vee-validate/rules';
import { localize } from '@vee-validate/i18n';
// Define the rule globally
Object.keys(AllRules).forEach((rule) => {
  defineRule(rule, AllRules[rule]);
});

configure({
  generateMessage: localize('vi', {
    messages: {
      required: 'Bắt buộc nhập',
      min_value: 'Chưa đạt số tối thiểu',
      max_value: 'Vượt qua số tối đa',
      max: 'Vượt qua số ký tự tối đa',
      min: 'Chưa đạt số ký tự tổi thiểu',
    },
  }),
});


// positive number rule
defineRule('positiveNumber', value => {
  // Field is empty, should pass
  if (!value || !value.length) {
    return true;
  }
  // check regex
  if (!/^\d*\.?\d+$/.test(value)) {
    return 'Vui lòng nhập số dương';
  }
  return true;
});

// string space before or after rule
defineRule('stringSpace', value => {
  // Field is empty, should pass
  if (!value || !value.length) {
    return true;
  }
  // check regex
  if (!/^\S(.*\S)?$/.test(value)) {
    return 'Không được có khoảng trắng trước hoặc sau dữ liệu nhập vào';
  }
  return true;
});

// check duplicate
defineRule('arrayDuplicate', (value, [array]) => {
  // The field is empty so it should pass
  if (!value || !value.length || !array) {
    return true;
  }
  // string to array
  let arrays = array.split('___');
  if (arrays.indexOf(value) !== arrays.lastIndexOf(value)) {
    return 'Dữ liệu bị trùng lặp';
  }
  return true;
});

// check total quantity a round
defineRule('maxQuantityRound', (value, [totalQuantity]) => {
  // The field is empty so it should pass
  if (!value || !value.length || !totalQuantity) {
    return true;
  }

  if (totalQuantity > 30) {
    return 'Tổng số lượng câu trả lời một vòng chơi không được quá 30';
  }
  return true;
});