import { encode, decode } from 'js-base64';
import router from '@/router'
import constant from '@/utils/constant';

// initial state
const state = () => ({
  userAuth: {},
});

// getters
const getters = {
  getUserAuth(state) {
    return state.userAuth
  },
};

// actions
const actions = {
  /*
  * Check login
  */
  async checkLogin({ commit }) {
    if (localStorage.getItem(constant.LOCAL_STORAGE_KEY.ACCESS_USER)) {
      // get user from local storage
      try {
        const user = JSON.parse(decode(localStorage.getItem(constant.LOCAL_STORAGE_KEY.ACCESS_USER)));
        commit('changeUserAuth', user)
        return
      } catch {
        // ignore
      }
    }
  },

  /**
   * Set user authentication to local storage variable
   *
   * @param {*} commit
   * @param {*} payload
   * @returns
   */
  async setUserAuth({ commit }, payload) {
    localStorage.setItem(constant.LOCAL_STORAGE_KEY.ACCESS_USER, encode(JSON.stringify(payload)));
    commit('changeUserAuth', payload)
    // default redirect
    return router.push({
      name: 'home',
    })
  },
};

// mutations
const mutations = {
  changeUserAuth(state, userAuth) {
    state.userAuth = userAuth;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
