// initial state
const state = () => ({
  title: '',
});

// getters
const getters = {
  getTitle(state) {
    return state.title
  },
};

// actions
const actions = {
  // set title
  setTitle({ commit }, title) {
    commit('setTitle', title)
  },
};

// mutations
const mutations = {
  setTitle(state, title) {
    state.title = title
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
