<template>
  <div>
    <div class="flex justify-center">
      <div class="login-form flex flex-col items-center pt-[40vh]">
        <Form @submit="onSubmit">
          <h1 class="text-[30px] mb-[16px]">
            Nhập họ và tên của bạn
          </h1>
          <div class="flex flex-col">
            <Field
              v-model="username"
              type="text"
              name="username"
              rules="required"
              class="form-default w-[250px] p-[15px]"
            />
            <ErrorMessage
              class="text-red-500 text-sm"
              name="username"
            />
          </div>
          <div class="text-center mt-6">
            <button
              class="btn-default w-[100px]"
              type="submit"
            >
              Đồng ý
            </button>
          </div>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
import { Form, Field, ErrorMessage } from 'vee-validate';
import { localize } from '@vee-validate/i18n';
import { mapActions } from 'vuex';

export default {
  components: {
    Form,
    Field,
    ErrorMessage,
  },
  data() {
    return {
      username: '',
    }
  },
  mounted() {
    // custom error message
    localize('vi', {
      fields:{
        username: {
          required: 'Vui lòng nhập tên của bạn',
        },
      },
    });
  },
  methods: {
    ...mapActions('authStore', ['setUserAuth']),
    // submit form
    onSubmit() {
      const user = {
        username: this.username
      }
      this.setUserAuth(user);
    }
  }
}
</script>